.modal {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  background-color: rgba(0, 0, 0, .9);
  z-index: 100;
}

.modal .closeButton {
  position: absolute;
  top: 16px;
  right: 16px;
}

.description {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: stretch;
      align-items: stretch;
  padding: 20px;
}

.imageStyle {
  height: 80vh;
}


